@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
:root {
  --accent-color: #FE7F67;
  --foreground-color: black;
  --background-color: #F6F6F6;
  --light-gray-color: #B2B2B2;
}

body, input, textarea, button {
  font-family: "Open Sans", sans-serif;
  font-size: 19px;
  color: black;
  color: var(--foreground-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-input-placeholder {
  color: #B2B2B2;
  color: var(--light-gray-color);
}

::-ms-input-placeholder {
  color: #B2B2B2;
  color: var(--light-gray-color);
}

::placeholder {
  color: #B2B2B2;
  color: var(--light-gray-color);
}

body {
  margin: 0;
  padding: 0;
  background-color: #F6F6F6;
  background-color: var(--background-color);
}

.body-small {
  text-align: center;
  width: 100%;
  max-width: 418px;
  /* background-color: gray; */
}

.what-we-do-item-container, .project-container, .form-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 90%;
  /* background-color: yellow; */
}

.what-we-do-item-container, .project-container {
  max-width: 698px;
}

.form-container {
  max-width: 600px;
}

.bigspace, .project-description-store-space, .recent-work-bigspace, .what-we-do-bigspace, .hire-bigspace {
  height: 60px;
  width: 60px;
}

.recent-work-bigspace, .hire-bigspace {
  height: 50px;
}

.smallspace, .recent-work-smallspace, .what-we-do-smallspace, .hire-smallspace {
  height: 15px;
  width: 15px;
}

.title {
  font-weight: 700;
  letter-spacing: -1.5px;
  text-align: center;
  font-size: 32px;
}

.line, .header-line, .border-line, .footer-line {
  height: 3px;
  background-color: black;
  background-color: var(--foreground-color);
}

.border-line {
  width: 100%;
}

.footer-line {
  width: 75%;
  max-width: 460px;
}

.header-line {
  width: 66%;
}

.what-we-do-end-space, .recent-work-end-space {
  height: 80px;
}

.form-feedback-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  min-height: 718px;
}

.form-attach-submit-space {
  height: 80px;
}

.hire-end-space {
  height: 120px;
}

.footer-content-vertical-space {
  height: 30px;
}

.footer-icons-logo-space {
  height: 50px;
}

.footer-logo-end-space {
  height: 40px;
}

@media only screen and (min-width: 600px) {
    .bigspace, .project-description-store-space {
      height: 70px;
      width: 70px;
    }

    .smallspace {
      height: 25px;
      width: 25px;
    }

    .title-space {
      height: 90px;
    }

    .what-we-do-end-space {
      height: 110px;
    }
  }
  
  @media only screen and (min-width: 615px) {
    .header-line {
      width: 87%;
    }
  }

  @media only screen and (min-width: 668px) {
    .recent-work-bigspace, .hire-bigspace {
      height: 90px;
    }
    
    .recent-work-end-space {
      height: 120px;
    }

    .form-feedback-container {
      min-height: 852px;
    }

    .hire-end-space {
      height: 150px;
    }
  }
  
  @media only screen and (min-width: 720px) {
    .what-we-do-item-container {
      -webkit-flex-direction: row;
              flex-direction: row;
    }
    
    .what-we-do-bigspace {
      height: 90px;
      width: 90px;
    }

    .what-we-do-smallspace {
      height: 30px;
      width: 30px;
    }

    .what-we-do-end-space {
      height: 120px;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .smallspace {
      height: 30px;
      width: 30px;
    }
  
    .title-space {
      height: 150px;
    }

    .what-we-do-item-container {
      -webkit-flex-direction: column;
              flex-direction: column;
      max-width: 30%;
    }

    .what-we-do-end-space {
      height: 100px;
    }
  }
.header-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.header-logo-top-space, .header-logo-title-space {
    height: 30px;
}

.header-logo-buttons-container, .header-art-container {
    width: 90%;
    max-width: 1024px;
}

.header-logo-buttons-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.header-buttons-container {
    display: none;
}

.header-art-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.header-art-title-line-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-right: 10%;
    max-width: 553px;
}

.header-title-line-space {
    height: 15px;
}

.header-art-title {
    font-size: 50px;
    font-weight: 700;
    letter-spacing: -4.07px;
    line-height: 42px;
    text-align: left;
}

@media only screen and (min-width: 375px) {
    .header-title-line-space {
        height: 20px;
    }
}

@media only screen and (min-width: 615px) {
    .header-logo-top-space {
        height: 60px;
    }
    
    .header-buttons-container {
        display: -webkit-flex;
        display: flex;
        margin-right: 15%;
    }
    
    .header-button {
        font-size: 20px;
        letter-spacing: -0.53px;
        background-color: unset;
        outline: none;
        border: none;
    }
    
    .header-buttons-horizontal-space {
        width: 40px;
    }
    
    .header-logo-title-space {
        height: 120px;
    }
    
    .header-art-title-line-container {
        margin-right: unset;
        -webkit-align-items: center;
                align-items: center;
    }
    
    .header-title-line-space {
        height: 25px;
    }
}
.recent-work-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background-color: var(--accent-color);
}

.project-details-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.store-button-bottom-space {
    height: 0px;
}

@media only screen and (min-width: 668px) {
    .project-container {
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-align-items: flex-end;
              align-items: flex-end;
    }
    
    .store-button-bottom-space {
      height: 20px;
    }

    .project-description-store-space {
        height: 170px;
    }
    
    .recent-work-bigspace {
      height: 90px;
    }
    
    .recent-work-end-space {
      height: 110px;
    }
}

.hire-me-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.test {
    background-color:black;
    width: 100%;
    height: 100%;
}

.input-email, .input-message, .attach-button, .submit-button {
    width: 100%;
    outline: none;
    background: none;
}

.input-email {
    border: none;
}

.input-message {
    resize: none;
    border-width: 3px;
    border-color: var(--foreground-color);
    border-radius: 10px;
    padding: 20px;
    box-sizing: border-box;
    height: 220px;
}

.attach-button, .submit-button {
    border-radius: 10px;
}

.attach-button {
    border-width: 1px;
    border-style: solid;
    border-color: var(--light-gray-color);
    padding-top: 20px;
    padding-bottom: 20px;
    color: var(--accent-color);
}

.submit-button {
    border: none;
    width: 220px;
    height: 70px;
    background-color: var(--accent-color);
    font-size: 22px;
    font-weight: 700;
    color: var(--background-color);
}

.footer-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.footer-icon-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.footer-icons-horizontal-space {
    width: 20px;
}

.footer-logo {
    width: 120px;
    height: 118px;
}
.what-we-do-main-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.what-we-do-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 90%;
    max-width: 1152px;
}

.image-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    min-width: 200px;
}


@media only screen and (min-width: 1024px) {
    .what-we-do-container {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-align-items: flex-end;
                align-items: flex-end;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }
}
